.layout {
  display: flex;
  flex-flow: column;
  width: 100vw;
  height: 100vh;
}

.wrapper {
  width: 100%;
  max-width: 1182px;
  padding: 5px 10px;
  margin: auto;
}

.header,
.main,
.footer {
  display: flex;
  justify-content: center;
  width: 100%;
  color: white;
  box-sizing: border-box;
}

.header {
  flex-wrap: wrap;
  flex: 0 1 auto;
}
.about,
.contacts {
  width: 100%;
}
.about {
  background-color: rgba(var(--background), 0.9);
  padding: 20px 0;
}
.contacts {
  background-color: rgba(var(--background-accent), 0.9);
  padding: 10px 0;
}

.main {
  flex: 1 1 auto;
  background-color: white;
  color: black;
}

.footer {
  flex: 0 1 auto;
  background-color: rgba(0, 56, 83, 0.5);
}

@media print {
  /* TODO: —ç—Ç–æ—Ç –æ–≤–µ—Ä–ª–µ–π –¥–ª—è –ø—Ä–∏–Ω—Ç–∏–Ω–≥–∞ –∑–∞—Ç–µ–Ω—è–µ—Ç –≤–µ—Å—å –∫–æ–Ω—Ç–µ–Ω—Ç */
  .header {
    box-shadow: inset 0 0 0 1000px rgba(var(--background), 0.9);
  }
  .footer {
    display: none;
  }
}
